$font-stack: Helvetica, sans-serif;
$primary-color: green;

* {
  padding: 0;
  margin: 0;
}

h1 {
  font-size: 10.5vw;
  color: $primary-color;
  background-color: #000;
  line-height: 2;
}

h2 {
  font-size: 20.5vw;
  color: $primary-color;
  background-color: red;
  display: flex;
  flex-direction: column;
  align-items: center;
}

ul {
  display: flex;
  list-style-type: none;
  li {
    width: calc(100% / 3);
    height: 30px;
    background-color: $primary-color;
    font-size: 30px;
  }
}